  import React,{useEffect,useRef,useState} from 'react';
  import { useNavigate } from 'react-router-dom';
  import { isAndroid, isIOS, isDesktop } from "react-device-detect";
  interface CameraProps {
    frame1: string;
    frame2:string;
    lang:any;
  }

  const Camera: React.FC<CameraProps> = ({frame1,frame2,lang}) => {
    const videoRef = useRef<HTMLVideoElement | null>(null);
    const canvasRef = useRef<HTMLCanvasElement | null>(null);
    const mediaRecorderRef = useRef<MediaRecorder | null>(null);
    const recordedChunksRef = useRef<Blob[]>([]);
    const [facingMode, setFacingMode] = useState<"user" | "environment">("user");
    const [orientation, setOrientation] = useState<"portrait" | "landscape">("portrait");
    const [frames, setFrames] = useState<boolean>(false);
    const navigate = useNavigate();
    const pressTimerRef = useRef<NodeJS.Timeout | null>(null);
    const [isLandscape, setIsLandscape] = useState<boolean>(false);
    const [isrecord,setIsRecord] = useState<boolean>(false);

  //  state 
  const [recordTime, setRecordTime] = useState<number>(0);  // 
  const [timerInterval, setTimerInterval] = useState<NodeJS.Timeout | null>(null);

  const [videoDevices, setVideoDevices] = useState<MediaDeviceInfo[]>([]);
  const [currentDeviceId, setCurrentDeviceId] = useState<string | null>(null);
  const [userCamera, setUserCamera] = useState<MediaDeviceInfo | null>(null);
  const [environmentCamera, setEnvironmentCamera] = useState<MediaDeviceInfo | null>(null);
  const [rotange,setrotange] = useState<boolean>(true);
  const [switchs,setswitchs] = useState<boolean>(true)
  
// 
const startTimer = () => {
  setRecordTime(0);  // 
  const interval = setInterval(() => {
    setRecordTime((prevTime) => prevTime + 1); // 
  }, 1000);
  setTimerInterval(interval);  //  interval 
};

const stopTimer = () => {
  if (timerInterval) {
    clearInterval(timerInterval); // 
  }
};
const stopExistingStream = () => {
  const videoElement = videoRef.current;
  if (videoElement && videoElement.srcObject) {
    const stream = videoElement.srcObject as MediaStream;
    stream.getTracks().forEach((track) => track.stop()); //  Track
    videoElement.srcObject = null; //  Stream 
  }
};
const getVideoDevices = async () => {
  try {
    const devices = await navigator.mediaDevices.enumerateDevices();
    const videoDevices = devices.filter(device => device.kind === 'videoinput');
    setVideoDevices(videoDevices);

    // 
    const userCamera = videoDevices.find(device => 
      device.label.toLowerCase().includes('front') || device.label.toLowerCase().includes('user')
    );
    const environmentCamera = videoDevices.find(device => 
      device.label.toLowerCase().includes('back') || device.label.toLowerCase().includes('environment')
    );

    setUserCamera(userCamera || null);
    setEnvironmentCamera(environmentCamera || null);
  } catch (error) {
    console.error('Error enumerating devices:', error);
  }
};
    // 
    const getCameraStream = async (deviceId:string) => {
      try {
        stopExistingStream()
        let videoConstraints: any;
        if(isIOS){
          videoConstraints = { facingMode:deviceId}
        }else{
          videoConstraints = { deviceId: { exact: deviceId },
          width: { ideal: 1280 },  // 
          height: { ideal: 720 },
          frameRate: { ideal: 30, max: 60 } }; //  exact  Android
        }
        const stream = await navigator.mediaDevices.getUserMedia({
          video: videoConstraints,
          audio: true, // 
        });

        if (videoRef.current) {
          videoRef.current.srcObject = stream;
        
        }
      } catch (error) {
        console.error("Error accessing the camera: ", error);
      }
    };

    const takePhoto = () => {
      if (videoRef.current && canvasRef.current) {
        const video = videoRef.current;
        const canvas = canvasRef.current;
        const context = canvas.getContext("2d");
    
        if (context) {
          // 

          const videoWidth = video.videoWidth;
          const videoHeight = video.videoHeight;
    
          // 
          const displayWidth = video.clientWidth;
          const displayHeight = video.clientHeight;
    
          //  "cover"
          const scale = Math.max(displayWidth / videoWidth, displayHeight / videoHeight);
          const cropWidth = videoWidth * scale;
          const cropHeight = videoHeight * scale;
    
          //  ()
          const cropX = (cropWidth - displayWidth) / 2 / scale;
          const cropY = (cropHeight - displayHeight) / 2 / scale;
    
          //  canvas 
          canvas.width = displayWidth;
          canvas.height = displayHeight;
          context.save();  //  canvas 
            //  180  
            if (rotange == true) {
              //  (flipped mirror)
              context.scale(-1, 1);  //  (flip image)
              context.translate(-canvas.width, 0);  //  canvas
            }
          //  canvas  "cover"
          context.drawImage(
            video,
            cropX, cropY,                          // 
            videoWidth - cropX * 2, videoHeight - cropY * 2, // 
            0, 0,                                 //  canvas
            canvas.width, canvas.height           //  canvas
          );
          context.setTransform(1, 0, 0, 1, 0, 0);
          context.restore();  // 
          // 
          const frameImage = new Image();
          console.log("Take Photo",frame1,frame2);

          frameImage.crossOrigin = "anonymous"; //  src

          frameImage.src = frames ? frame1 : frame2;
          frameImage.onload = () => {
            context.drawImage(frameImage, 0, 0, canvas.width, canvas.height);
    
            // 
            const imageData = canvas.toDataURL("image/png");
            navigate("/"+lang+"/social", { state: { imageData } });
          };
          frameImage.onerror = (error) => {
            console.error("Failed to load image:", error, frameImage.src);
          };
        }
      }
    };

    

    // const startRecording = () => {
    //   const stream = videoRef.current?.srcObject as MediaStream;
    
    //   //  Canvas  Stream
    //   if (!canvasRef.current || !stream || !videoRef.current) {
    //     console.error("Canvas, Stream, or Video not ready!");
    //     return;
    //   }
    
    //   setIsRecord(true);
    //   startTimer();  // 
    //   const canvas = canvasRef.current;
    //   const context = canvas.getContext("2d")!;
    //   const video = videoRef.current;
    
    //   const [track] = stream.getVideoTracks();
    //   const videoSettings = track.getSettings();
    
    //   //  cover 
    //   const frameImage = new Image();
    //   frameImage.src = frames ? frame1 : frame2;
    
    //   frameImage.onload = () => {
    //     console.log("Cover image loaded");
    
    //     // 
    //     const videoWidth = video.videoWidth;
    //     const videoHeight = video.videoHeight;
    
    //     // 
    //     const displayWidth = video.clientWidth;
    //     const displayHeight = video.clientHeight;
    
    //     //  "cover"
    //     const scale = Math.max(displayWidth / videoWidth, displayHeight / videoHeight);
    //     const cropWidth = videoWidth * scale;
    //     const cropHeight = videoHeight * scale;
    
    //     //  ()
    //     const cropX = (cropWidth - displayWidth) / 2 / scale;
    //     const cropY = (cropHeight - displayHeight) / 2 / scale;
    
    //     //  Canvas 
    //     canvas.width = displayWidth;
    //     canvas.height = displayHeight;
    
    //     //  stream  canvas
    //     const canvasStream = canvas.captureStream();
    //     if (!canvasStream) {
    //       console.error("Failed to capture stream from canvas");
    //       return;
    //     }
    
    //     //  MediaRecorder
    //     const mimeType = MediaRecorder.isTypeSupported("video/webm") ? "video/webm" :
    //                     MediaRecorder.isTypeSupported("video/mp4") ? "video/mp4" :
    //                     "video/ogg";
    
    //     if (!mimeType) {
    //       console.error("No supported MIME type found.");
    //       return;
    //     }
    
    //     mediaRecorderRef.current = new MediaRecorder(canvasStream, { mimeType });
    //     recordedChunksRef.current = [];
    
    //     //  canvas
    //     const drawFrame = () => {
    //       if (context) {
    //         //  canvas 
    //         context.clearRect(0, 0, canvas.width, canvas.height);
    //         context.save();  //  canvas 
    //         //  180  
    //         if (rotange == true) {
    //           //  (flipped mirror)
    //           context.scale(-1, 1);  //  (flip image)
    //           context.translate(-canvas.width, 0);  //  canvas
    //         }

    //         // 
    //         context.drawImage(
    //           video,
    //           cropX, cropY,                         // 
    //           videoWidth - cropX * 2, videoHeight - cropY * 2, // 
    //           0, 0,                                  //  canvas
    //           canvas.width, canvas.height            //  canvas
    //         );
    //         context.scale(-1, 1);
    //         //  cover 
    //         // 
    //         context.setTransform(1, 0, 0, 1, 0, 0);
    //         context.restore();  // 
    //         context.drawImage(frameImage, 0, 0, canvas.width, canvas.height);

    //       }
    //       requestAnimationFrame(drawFrame); // 
    //     };
    
    //     drawFrame(); // 
    
    //     // Event: 
    //     mediaRecorderRef.current.ondataavailable = (event) => {
    //       if (event.data.size > 0) {
    //         recordedChunksRef.current.push(event.data);
    //       }
    //     };
    
    //     // Event: 
    //     mediaRecorderRef.current.onstop = () => {
    //       stopTimer();
    //       if (recordedChunksRef.current.length > 0) {
    //         const blob = new Blob(recordedChunksRef.current, { type: mimeType });
    //         const videoDataUrl = URL.createObjectURL(blob);
    //         navigate("/social", { state: { videoDataUrl } }); //  base64  URL  SocialShare
    //       }
    //       setIsRecord(false);
    //     };
    
    //     // 
    //     mediaRecorderRef.current.start();
    //     console.log("Recording with Cover started");
    
    //     //  10 
    //     setTimeout(() => {
    //       if (mediaRecorderRef.current?.state === "recording") {
    //         mediaRecorderRef.current.stop();
    //         console.log("Recording stopped");
    //       }
    //     }, 11000);
    //   };
    
    //   //  cover 
    //   frameImage.onerror = () => {
    //     console.error("Failed to load cover image");
    //   };
    // };

    const startRecording = async () => {
      const stream = videoRef.current?.srcObject as MediaStream;
    
      if (!canvasRef.current || !stream || !videoRef.current) {
        console.error("Canvas, Stream, or Video not ready!");
        return;
      }
    
      setIsRecord(true);
      startTimer();
      const canvas = canvasRef.current;
      const context = canvas.getContext("2d")!;
      const video = videoRef.current;
    
      const [track] = stream.getVideoTracks();
      const videoSettings = track.getSettings();
    
      //  cover
      const frameImage = new Image();
      frameImage.crossOrigin = "anonymous"; //  src

      frameImage.src = frames ? frame1 : frame2;
    
      frameImage.onload = async () => {
        const videoWidth = video.videoWidth;
        const videoHeight = video.videoHeight;
    
        const displayWidth = video.clientWidth;
        const displayHeight = video.clientHeight;
    
        const scale = Math.max(displayWidth / videoWidth, displayHeight / videoHeight);
        const cropWidth = videoWidth * scale;
        const cropHeight = videoHeight * scale;
    
        const cropX = (cropWidth - displayWidth) / 2 / scale;
        const cropY = (cropHeight - displayHeight) / 2 / scale;
    
        canvas.width = displayWidth;
        canvas.height = displayHeight;
    
        //  stream  canvas
        const canvasStream = canvas.captureStream();
    
        // ****
        let audioStream: MediaStream | null = null;
        try {
          audioStream = await navigator.mediaDevices.getUserMedia({ audio: true });
        } catch (error) {
          console.error("Error accessing microphone:", error);
        }
    
        if (audioStream) {
          //  stream  stream 
          audioStream.getAudioTracks().forEach((audioTrack) => {
            canvasStream.addTrack(audioTrack); //  canvasStream
          });
        }
    
        const mimeType = MediaRecorder.isTypeSupported("video/webm;codecs=vp9,opus")
          ? "video/webm;codecs=vp9,opus"
          : MediaRecorder.isTypeSupported("video/webm")
          ? "video/webm"
          : "video/mp4";
    
        mediaRecorderRef.current = new MediaRecorder(canvasStream, { mimeType });
        recordedChunksRef.current = [];
    
        const drawFrame = () => {
          if (context) {
            context.clearRect(0, 0, canvas.width, canvas.height);
            context.save();
    
            if (rotange === true) {
              context.scale(-1, 1);
              context.translate(-canvas.width, 0);
            }
    
            context.drawImage(
              video,
              cropX,
              cropY,
              videoWidth - cropX * 2,
              videoHeight - cropY * 2,
              0,
              0,
              canvas.width,
              canvas.height
            );
    
            context.setTransform(1, 0, 0, 1, 0, 0);
            context.restore();
            context.drawImage(frameImage, 0, 0, canvas.width, canvas.height);
          }
          requestAnimationFrame(drawFrame);
        };
    
        drawFrame();
    
        mediaRecorderRef.current.ondataavailable = (event) => {
          if (event.data.size > 0) {
            recordedChunksRef.current.push(event.data);
          }
        };
    
        mediaRecorderRef.current.onstop = () => {
          stopTimer();
          if (recordedChunksRef.current.length > 0) {
            const blob = new Blob(recordedChunksRef.current, { type: mimeType });
            const videoDataUrl = URL.createObjectURL(blob);
            navigate("/"+lang+"/social", { state: { videoDataUrl } });
          }
          setIsRecord(false);
        };
    
        mediaRecorderRef.current.start();
        console.log("Recording with audio started");
    
        setTimeout(() => {
          if (mediaRecorderRef.current?.state === "recording") {
            mediaRecorderRef.current.stop();
            console.log("Recording stopped");
          }
        }, 11000);
      };
    
      frameImage.onerror = () => {
        console.error("Failed to load cover image");
      };
    };
    //  UI 
    const formatTime = (time: number) => {
      const minutes = Math.floor(time / 60);
      const seconds = time % 60;
      return `${minutes}:${seconds < 10 ? "0" : ""}${seconds}`;
    };
    // 
    const detectOrientation = () => {
      if (window.matchMedia("(orientation: landscape)").matches) {
        setOrientation("landscape");
        setFrames(false);
      } else {
        setOrientation("portrait");
        setFrames(true);
      }
      setIsLandscape(window.matchMedia('(orientation: landscape)').matches);
    };

    // 
    const handleMouseDown = () => {
      pressTimerRef.current = setTimeout(() => {
        console.log("Long Press: Start recording...");
        startRecording();
      }, 1500); //  3 
    };

    const handleMouseUp = () => {
      if (pressTimerRef.current) {
        clearTimeout(pressTimerRef.current);
        
        if(isrecord === false){
          console.log("Short Press: Take photo...");
          takePhoto();
        }
      }
    };

    // 
    const handleTouchStart = (event: React.TouchEvent) => {
      event.preventDefault(); //  context menu
      event.stopPropagation();
      pressTimerRef.current = setTimeout(() => {
        console.log("Long Press: Start recording...");
        startRecording();
      }, 1000);
    };

    const handleTouchEnd = (event: React.TouchEvent) => {
      event.preventDefault();
      event.stopPropagation();
      if (pressTimerRef.current) {
        clearTimeout(pressTimerRef.current);

        if(isrecord === false){
          console.log("Short Press: Take photo...");
          takePhoto(); // 
        }
      }
    };
    useEffect(() => {
      getVideoDevices();
      return () => {
        if (videoRef.current?.srcObject) {
          const stream = videoRef.current.srcObject as MediaStream;
          stream.getTracks().forEach((track) => track.stop());
          videoRef.current.srcObject = null;
        }
      };
    }, []);
    useEffect(() => {
      stopExistingStream()
      if(userCamera && environmentCamera && !currentDeviceId){
        
        setTimeout(()=>{
          const newDeviceId = facingMode === "user" ? userCamera.deviceId : environmentCamera.deviceId;
          if(facingMode === "user"){
            setrotange(true);
          }else{
            setrotange(false);
          }
          getCameraStream(newDeviceId);
          setswitchs(true);
        },50);
        
      }else{
        if(isIOS){
          setTimeout(()=>{
          getCameraStream(facingMode);
          if(facingMode === "user"){
            setrotange(true);
          }else{
            setrotange(false);
          }
          setswitchs(true);
          },50);
        }else if(isDesktop || isAndroid){
          setTimeout(()=>{
          getCameraStream("");
          setswitchs(false);
          },50);
        }
      }
      detectOrientation();

      window.addEventListener("resize", detectOrientation);
      return () => {
        window.removeEventListener("resize", detectOrientation);
      };
    }, [userCamera, environmentCamera, currentDeviceId,facingMode]);
// 
  const handleContextMenu = (event: React.MouseEvent) => {
    event.preventDefault(); // 
  };
    return (
      <div
        onContextMenu={handleContextMenu} // 
        style={{
          height: '100vh',
          display: 'flex',
          flexDirection: isLandscape ? 'row' : 'column', // 
          backgroundColor: 'black',
        }}
      >
        {/*  */}
        <div style={{ flex: 1, position: 'relative' }}>
          <video
            ref={videoRef}
            autoPlay
            playsInline
            muted
            style={{
              width: '100%',
              height: '100%',
              objectFit: 'cover',
              transform: rotange === true ? 'scaleX(-1)' : 'none'
            }}
          />
          {/* Overlay  */}
          <div
            style={{
              position: 'absolute',
              top: 0,
              left: 0,
              width: '100%',
              height: '100%',
              pointerEvents: 'none',
              backgroundImage: `url(${frames ? frame1 : frame2})`,
              backgroundSize: 'cover',
              backgroundPosition: 'center',
            }}
          />
        </div>

        {/*  */}
        
        <div
          style={{
            width: isLandscape ? '13%' : '100%', // 
            height: isLandscape ? '100%' : '13%', // 
            backgroundColor: 'black',
            display: 'flex',
            justifyContent: isLandscape ? 'center' : 'space-between', //  
            alignItems: 'center',
            flexDirection: isLandscape ? 'column' : 'row', // 
            padding: isLandscape ? '20px 0' : '0 20px',
            position: 'relative', //  relative 
          }}
        >
          {/*  */}
        {isrecord && (
          <div
            style={{
              position: 'absolute',
              top: '50%', // 
              left: '50%', // 
              transform: 'translate(-50%, -50%)', // 
              fontSize: '24px',
              color: 'white',
              padding: '10px',
              borderRadius: '5px',
              zIndex: 5,
              marginTop: isLandscape ? "0px" : "-53px",
              marginLeft: isLandscape ? "-53px" : "0px"
            }}
          >
            {formatTime(recordTime)}
          </div>
        )}
          {/*  -  */}
          {switchs && (
          <button
            onClick={() => {
              const newMode = facingMode === 'user' ? 'environment' : 'user';
              setFacingMode(newMode);
            }}
            style={{
              width: '50px',
              height: '50px',
              borderRadius: '50%',
              background: 'url("../image-bt2.png") center/cover no-repeat',
              border: 'none',
              position: isLandscape ? 'static' : 'absolute', //  absolute 
              right: isLandscape ? 'auto' : '20px', // 
              top: isLandscape ? 'auto' : '50%', // 
              transform: isLandscape ? 'none' : 'translateY(-50%)', //  translateY 
              marginTop: isLandscape ? "-235px" : "-53px",
              marginLeft: isLandscape ? "-109px" : "0px"
            }}
          />
          )}
          {/*  -  */}
          <button
            // onMouseDown={handleMouseDown}
            // onMouseUp={handleMouseUp}
            onTouchStart={handleTouchStart}
            onContextMenu={handleContextMenu} // 
            onTouchEnd={handleTouchEnd}
            style={{
              width: '80px',
              height: '80px',
              borderRadius: '50%',
              background: `url(${isrecord ? "../image-bt1-red.png" : "../image-bt1.png"}) center/cover no-repeat`,
              border: 'none',
              boxShadow: '0 4px 6px rgba(255, 255, 255, 0.3)',
              position: 'absolute', //  absolute 
              left: '50%',
              transform: 'translateX(-50%)', // 
              marginTop: isLandscape ? "0px" : "-113px",
              marginLeft: isLandscape ? "-53px" : "0px"
            }}
          />

          {/* Spacer  Layout */}
          <div style={{ width: '60px', height: '60px' }} />
        </div>

        {/* Canvas  */}
        <canvas ref={canvasRef} style={{ display: 'none' }} />
        {/* <div className="mt-4 p-4 border rounded-lg bg-white w-3/4 shadow-md">
        <h2 className="text-md font-bold mb-2">Camera Information</h2>
        {userCamera && (
          <div className="flex justify-between">
            <span>Front Camera:</span>
            <span>{userCamera.label || "Unnamed"}</span>
            <span className="text-gray-500 text-sm">ID: {userCamera.deviceId}</span>
          </div>
        )}
        {environmentCamera && (
          <div className="flex justify-between mt-2">
            <span>Back Camera:</span>
            <span>{environmentCamera.label || "Unnamed"}</span>
            <span className="text-gray-500 text-sm">ID: {environmentCamera.deviceId}</span>
          </div>
        )}
      </div> */}
      </div>
    );
  };

  export default Camera;